<template>
  <div>
    <h1>欢迎登录商城后台管理系统，当前时间：{{ date | dateFormat }}</h1>
    <el-calendar v-model="date"></el-calendar>
  </div>
</template>

<script>
export default {
  filters: {
    dateFormat(val) {
      const dt = val;
      const y = dt.getFullYear();
      const m = dt.getMonth() + 1;
      const d = dt.getDate();
      const hh = dt.getHours();
      const mm = dt.getMinutes();
      return `${y}年${m}月${d}日${hh}时${mm}分`;
    },
  },
  data() {
    return {
      date: new Date(),
    };
  },
};
</script>

<style lang="less" scoped>
div {
  padding: 20px;
  h1 {
    font-size: 25px;
  }
  .el-calendar {
    margin: 50px;
  }
}
</style>